.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 0.2rem;
  background: linear-gradient(
    45deg,
    transparent,
    rgb(255, 255, 255),
    transparent
  );
  display: inline-block;
  padding: 15px 15px 70px;
  text-align: center;
}

.about__me-img {
  border-radius: 0.2rem;
  overflow: hidden;
  transform: rotate(0);
  transition: var(--transition);
}

.about__me-img:hover {
  transform: rotate(360deg);
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.about__card {
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}

.about__card:hover {
  font-size: 0.95rem;

}

.about__card small {
  font-size: 0.7rem;
  
}

.about__content p {
  margin: 2rem 0 2.6rem;
  color: white;
}

/* ==================== MEDIA QUERIES (MEDIUM)=============*/

@media screen and (max-width: 1024px) {
  .about__container {
      grid-template-columns: 1fr;
      gap:0;
  }

  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
  .about__content p{
    margin: 1rem 0 1.5rem;
  }
}

/* ==================== MEDIA QUERIES (SMALL)=============*/

@media screen and (max-width: 600px) {
  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }

  .about__content {
    text-align: center;
  }

  .about__content p {
    margin: 1.5rem 0;
  }
}